import React from 'react';
import { Map, TileLayer, useLeaflet } from 'react-leaflet';

import MarkerCluster from './MarkerCluster';
import { markers } from '../../data/dashboard/activeUsers';
import { useEffect } from 'react';
import 'leaflet.tilelayer.colorfilter';
import L from 'leaflet';
import { useRef } from 'react';
import { useState, useContext } from 'react';
import AppContext from '../../context/Context';

const Leaflet = ({ mapWidth, mapHeight }) => {
  const { isDark } = useContext(AppContext);
  const myFilterDark = ['invert:98%', 'grayscale:69%', 'bright:89%', 'contrast:111%', 'hue:205deg', 'saturate:1000%'];
  const myFilter = ['bright:101%', 'contrast:101%', 'hue:23deg', 'saturate:225%'];

  let map = useRef(null);

  if (mapWidth && mapHeight) {
    console.log('mapWidth', mapWidth);
    console.log('mapHeight', mapHeight);
    setTimeout(() =>
      L.tileLayer
        .colorFilter('https://{s}.basemaps.cartocdn.com/light_all/{z}/{x}/{y}{r}.png', {
          attribution: null,
          transparent: true,
          filter: isDark ? myFilterDark : myFilter
        })
        .addTo(map.current.leafletElement)
    );
    return (
      <Map
        center={[10.737, 0]}
        zoom={1}
        minZoom={1}
        maxZoom={20}
        zoomSnap={0.5}
        className="h-100"
        style={{ width: '100%', minHeight: 300 }}
        ref={map}
      >
        <MarkerCluster markers={markers} />
      </Map>
    );
  }
  return null;
};

export default Leaflet;
